import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuery, VhQueryCafe } from 'ionic-vhframeworks';
import { DestroyProductK57Col3Component } from 'src/app/cafe/components/print/destroy-product/destroy-product-k57-col3/destroy-product-k57-col3.component';
import { DestroyProductK80Col3Component } from 'src/app/cafe/components/print/destroy-product/destroy-product-k80-col3/destroy-product-k80-col3.component';
import { DestroyProductK80Col4Component } from 'src/app/cafe/components/print/destroy-product/destroy-product-k80-col4/destroy-product-k80-col4.component';
import { DestroyProductA5Component } from 'src/app/cafe/components/print/destroy-product/destroy-product-a5/destroy-product-a5.component';
import { DestroyProductA4Component } from 'src/app/cafe/components/print/destroy-product/destroy-product-a4/destroy-product-a4.component';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component'; 
import { LanguageService } from 'src/app/cafe/services/language.service';
import { FunctionService } from 'src/app/cafe/services/function.service';
import { VhBillType38DetailEdit, VhBillType38Edit } from '../../bill-type/bill-type-38/bill-type38-interface';

@Component({
  selector: 'app-destroy-product',
  templateUrl: './destroy-product.component.html',
  styleUrls: ['./destroy-product.component.scss']
})
export class DestroyProductComponent  {

  @Input() id_bill: any;
  tableHeight: string;
  public invoiceInfo: any;
  invoice_detail  : VhBillType38DetailEdit[] = [] 
  public invoice: VhBillType38Edit
  printer: any;
  listBarcodeLot = [];
  constructor(
    private router: Router,
    private vhQueryCafe: VhQueryCafe,
    private languageService: LanguageService,
    private vhAuth: VhAuth,
    private vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private cdRef: ChangeDetectorRef,
    private functionService: FunctionService,
    private vhQuery: VhQuery
  ) {
    this.init();
    let print = this.vhQueryCafe.getlocalPrinters().find(item => item.desktop)
    this.printer = print ? print.desktop.purchase[print.desktop.purchase.type] : { discount_hidden: false, note_hidden: false, fee_hidden: false, tax_hidden: false, debt_hidden: false };
  }


  ngAfterViewInit(): void {
    this.getDataInvoice(this.id_bill)
     
  }
  init(invoice: VhBillType38Edit = null, invoiceDetail: VhBillType38DetailEdit[] = null) {
    console.log(invoice);
    
    this.invoice = invoice
    this.invoice_detail = invoiceDetail
    if (invoice && invoiceDetail) {
      this.invoiceInfo = {
        employee: this.vhAuth.getlocalEmployee(this.invoice.id_employee),
      }
    } else this.invoiceInfo = { employee: {} }
   
  }
  private getDataInvoice(id) {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      Promise.all([
        this.vhQueryCafe.getBill(id),
        this.vhQueryCafe.getBill_details_byId_bill(id),
        this.vhQuery.getDocsByFields("barcode_designs", { id_branch: { $eq: this.vhQueryCafe.getDefaultBranch()._id }, type: { $eq: 5 } })
      ]).then(([invoice, invoiceDetail, list_barcode]: any) => {
        this.listBarcodeLot = list_barcode.map(item => { item.property = this.vhAlgorithm.sortNumberbyASC(item.property, 'index'); return { ...item } })

        if (!invoiceDetail.length)
          this.init(invoice, [])
        else {
          invoiceDetail = this.vhAlgorithm.sortVietnamesebyASC(invoiceDetail, 'name');
          this.init(invoice, invoiceDetail)
        }

        this.vhComponent.hideLoading(0)
      })
    })

  }

  getUnit(units: any[], ratio) {
    return this.vhQueryCafe.getUnit_byRatio(units, ratio).unit
  }
  getLotNumber(lots, _id) {
    let lot = lots.find(item => item._id == _id)
    return lot.lot_number
  }

  getDateLotNumber(lots, _id) {
    let lot = lots.find(item => item._id == _id)
    return this.functionService.formatDate(lot.date_mfg) + ' - ' + this.functionService.formatDate(lot.date_exp)
  }
  deleteInvoice() {
    if (this.vhAuth.checkMyPermission("purchase_enable_cancel_bill")) {
      this.vhComponent.alertConfirm(this.languageService.translate("Confirm"), "", `${this.languageService.translate("Are you sure to cancel the purchase invoice")}?`, "OK", this.languageService.translate("Cancel"))
        .then(() => {
          this.vhComponent.showLoading("", "transparent-loading").then(() => {
            this.vhQueryCafe.deleteBill_Billdetail(this.invoice._id).then(() => {
              this.vhComponent.hideLoading(0).then(() => {
                this.vhComponent.showToast(2000,
                  `${this.languageService.translate("Purchase invoice")} ${this.invoice.bill_code} ${this.languageService.translate("has been cancelled successfully")}`, "success-toast")
                this.goBack();
              })
            })
          })
        }, () => { })
    }
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  public renderAddress(partner) {
    return (partner.address ? partner.address : '') +
      (partner.district ? ', ' + partner.district : '') +
      (partner.province ? ', ' + partner.province : '') +
      (partner.country ? ', ' + partner.country : '')
  } 
   /**
   * lấy thông tin dữ liệu trang in
   * @returns Promise<any>
   * @example this.checkPrint()
   */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQueryCafe.getLocalPrintPage('page_desktop_destroy_product')
      if (printer && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }

  printInvoice() {
    this.checkPrint().then((printer) => {
      if (printer)
        this.vhComponent.showModal(this.renderPrintPage(printer['_id']), { printer: printer, invoice_detail : this.invoice_detail,invoice: this.invoice }, false, false, `modal-print-${printer['_id']}`)
    }, (err) => {
    
      this.vhComponent.alertMessage(this.languageService.translate("Function"), "", this.languageService.translate("Please turn on the printer function"), "OK")
         
    })
  }

  /**
   * trả về component của trang in tương ứng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return (DestroyProductK57Col3Component)
      case 'print_size_k80_3c': return (DestroyProductK80Col3Component)
      case 'print_size_k80_4c': return (DestroyProductK80Col4Component)
      case 'print_size_a5': return (DestroyProductA5Component)
      case 'print_size_a4': return (DestroyProductA4Component)
      default: return (DestroyProductA4Component)
    }
  }
  goBack() {
    this.vhComponent.hideModal()
  }
  ngAfterViewChecked() {
    if (document.querySelector("#purchase-invoice-detail-today") && document.querySelector(".ant-table-thead") && document.querySelector(".sales-invoice-detail-today-header") && document.querySelector(".ant-table-pagination")) {
      this.tableHeight = document.querySelector("#purchase-invoice-detail-today").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".sales-invoice-detail-today-header").clientHeight - document.querySelector(".ant-table-pagination").clientHeight - 40 + "px";
    }
    this.cdRef.detectChanges();
  }
}
