import { ChangeDetectorRef, Component, Input } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuery, VhQueryCafe } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service'; 
import { ExchangePaymentCardK57Col3Component } from 'src/app/cafe/components/print/prepaid-card/exchange-payment-card-k57-col3/exchange-payment-card-k57-col3.component';
import { ExchangePaymentCardK80Col4Component } from 'src/app/cafe/components/print/prepaid-card/exchange-payment-card-k80-col4/exchange-payment-card-k80-col4.component';
import { ExchangePaymentCardK80Col3Component } from 'src/app/cafe/components/print/prepaid-card/exchange-payment-card-k80-col3/exchange-payment-card-k80-col3.component';
import { ExchangePaymentCardA5Component } from 'src/app/cafe/components/print/prepaid-card/exchange-payment-card-a5/exchange-payment-card-a5.component';
import { ExchangePaymentCardA4Component } from 'src/app/cafe/components/print/prepaid-card/exchange-payment-card-a4/exchange-payment-card-a4.component';
import { VhBillType51DetailEdit, VhBillType51Edit } from '../../bill-type/bill-type-51/bill-type51-interface';


@Component({
  selector: 'app-exchange-payment-card',
  templateUrl: './exchange-payment-card.component.html',
  styleUrls: ['./exchange-payment-card.component.scss']
})
export class ExchangePaymentCardComponent   {

  display_promotion_selling_price: boolean = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').display_promotion_selling_price
  tableHeight: string;
  public invoiceInfo: any;
  public invoice: VhBillType51Edit;
  invoiceDetail: VhBillType51DetailEdit[];
  dataRestore: any;
  total_product: number = 0;
  bill_type: any;
  root: any;
  product_codes: any;
  list_product_print: any = [];
  @Input() id_bill: any;
  constructor(
    private router: Router,
    public vhQueryCafe: VhQueryCafe,
    private languageService: LanguageService,
    public vhAuth: VhAuth,
    private vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private cdRef: ChangeDetectorRef,
    private vhQuery: VhQuery
  ) {
     
   
  }

  ngAfterViewInit(): void {
    this.getDataInvoice(this.id_bill)
     
  }

  init(bill_type, invoice: VhBillType51Edit = null, invoiceDetail: VhBillType51DetailEdit[] = null) {
    if (bill_type == 51) this.invoice = invoice
    this.invoiceDetail = invoiceDetail
    console.log(this.invoiceDetail);
    
    if (invoice && invoiceDetail) {
      this.invoiceInfo = {
        employee: this.vhAuth.getlocalEmployee(this.invoice.id_employee),
        partner: this.vhQueryCafe.getlocalCustomer(this.invoice.id_customer),

      }
    } else this.invoiceInfo = { employee: {}, partner: {}, paymentName: '' }
    this.total_product = 0
    for (let i of this.invoiceDetail) {
      this.total_product += i.products.reduce((prev: number, next) => prev + next.quantity, 0)
    }
  }

  private getDataInvoice(id) {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      Promise.all([
        this.vhQueryCafe.getBill(id),
        this.vhQueryCafe.getBill_details_byId_bill(id)
      ]).then(([invoice, invoiceDetail]: any) => {
        console.log(invoiceDetail);
        if (invoice.bill_type == 51) {
          let promise = new Array();
          for (let i = 0; i < invoiceDetail.length; i++) {
            promise[i] = this.vhQueryCafe.getPaymentCard_Code_Name_Release(invoiceDetail[i].id_payment_card_code)
          }
          this.vhQuery.asyncPromiseAll(promise).then((array) => {
            let invoiceDetailFresh = invoiceDetail;
            console.log(array);
            
            invoiceDetailFresh = array.map((item, index) => {
              return {
                name_payment_card_release : item.name_payment_card_release,
                name_payment_card  : item.name_payment_card,
                code : item.code,
                products: invoiceDetail[index].products.map(prod_child => {
                  return { 
                    name: prod_child.name,
                    units: prod_child.units,
                    ratio: prod_child.ratio,
                    quantity_use: prod_child.quantity,
                    outquantity: prod_child.outquantity ,
                    restquantity: prod_child.restquantity,
                    quantity: item.products.find(e => prod_child.id_subproduct ? ( e.id_subproduct == prod_child.id_subproduct) : ( e.id_product == prod_child.id_product)).quantity
                  }
                })
              }
            })
            this.init(invoice.bill_type, invoice, invoiceDetailFresh)
            setTimeout(() => {
              this.product_codes = invoiceDetailFresh;
              console.log(this.product_codes);
              
            }, 200);
            this.vhComponent.hideLoading(0);
          }, error => {
            console.log(error);
            this.vhComponent.hideLoading(0);

          })
        }

      })
    })
  }

  getUnit(units: any[], ratio) {
    return this.vhQueryCafe.getUnit_byRatio(units, ratio).unit
  }
  getLotNumber(lots, _id) {
    let lot = lots.find(item => item._id == _id);
    return lot.lot_number
  }
  ngAfterViewChecked() {
    if (document.querySelector("#sales-return-invoice-detail") && document.querySelector(".ant-table-thead") && document.querySelector(".sales-return-invoice-detail-header")) {
      this.tableHeight = document.querySelector("#sales-return-invoice-detail").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".sales-return-invoice-detail-header").clientHeight - 60 + "px";
      if (document.querySelector(".ant-table-pagination")) {
        this.tableHeight = document.querySelector("#sales-return-invoice-detail").clientHeight - document.querySelector(".ant-table-thead").clientHeight
          - document.querySelector(".sales-return-invoice-detail-header").clientHeight - document.querySelector(".ant-table-pagination").clientHeight - 60 + "px";
      }
    }

    this.cdRef.detectChanges();
  }

  goBack() {
    this.router.navigate(['/cafe/dashboard/management/sales-program/card-goods/exchange-card-invoice'], { state: this.dataRestore });
  }

  printInvoice() {
    let print = this.vhQueryCafe.getLocalPrintPage('page_desktop_exchange_payment_card')
    if (print && print.print_sizes[print.print_sizes.findIndex(e => e._id == print.print_size_default)].enable) {
      let printer =  print.print_sizes[print.print_sizes.findIndex(e => e._id == print.print_size_default)] 
      if (printer) {
        let type = printer._id;
        this.vhComponent.showModal(this.renderPrintPage(type), { printer: printer , invoice: this.invoice ,invoice_detail:this.invoiceDetail}, false, false, `modal-print-${type}`).then((modal) => {
          modal.onWillDismiss().then(() => this.goBack())
        })
      }
    }
  }



  /**
   * trả về component của trang in tương ứng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return ExchangePaymentCardK57Col3Component
      case 'print_size_k80_3c': return ExchangePaymentCardK80Col3Component
      case 'print_size_k80_4c': return ExchangePaymentCardK80Col4Component
      case 'print_size_a5': return ExchangePaymentCardA5Component
      case 'print_size_a4': return ExchangePaymentCardA4Component
      default: return ExchangePaymentCardA4Component
    }
  }

  deleteInvoice() {
    if (this.vhAuth.checkMyPermission("sell_enable_cancel_bill")) {
      this.vhComponent.alertConfirm(this.languageService.translate("Confirm"), "", `${this.languageService.translate("Are you sure to cancel this invoice")}?`, "OK", this.languageService.translate("Cancel"))
        .then(() => {
          this.vhComponent.showLoading("", "transparent-loading").then(() => {
            this.vhQueryCafe.deleteBill_Billdetail(this.invoice._id).then(() => {
              this.vhComponent.hideLoading(0).then(() => {
                this.vhComponent.alertMessageDesktop("success",
                  `${this.languageService.translate("Sales invoice")} ${this.invoice.bill_code} ${this.languageService.translate("has been cancelled successfully")}`)
                this.router.navigate(['/cafe/dashboard/management/sales-program/card-goods/exchange-card-invoice'], { state: { ...this.dataRestore, reload: true } })
              })
            })
          })
        }, () => { })
    }
    else this.vhComponent.alertMessageDesktop("error", this.languageService.translate('You do not have this rights'))
  }


  public renderAddress(partner) {
    return (partner.address ? partner.address : '') +
      (partner.district ? ', ' + partner.district : '') +
      (partner.province ? ', ' + partner.province : '') +
      (partner.country ? ', ' + partner.country : '')
  }
 
}
