import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core';
import { ActivatedRouteSnapshot, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQueryCafe } from 'ionic-vhframeworks';
import { Observable } from 'rxjs';

import { ReceiveGoodsA4Component } from 'src/app/cafe/components/print/receive-goods/receive-goods-a4/receive-goods-a4.component';
import { ReceiveGoodsA5Component } from 'src/app/cafe/components/print/receive-goods/receive-goods-a5/receive-goods-a5.component';
import { ReceiveGoodsK57Col3Component } from 'src/app/cafe/components/print/receive-goods/receive-goods-k57-col3/receive-goods-k57-col3.component';
import { ReceiveGoodsK80Col3Component } from 'src/app/cafe/components/print/receive-goods/receive-goods-k80-col3/receive-goods-k80-col3.component';
import { ReceiveGoodsK80Col4Component } from 'src/app/cafe/components/print/receive-goods/receive-goods-k80-col4/receive-goods-k80-col4.component';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component'; 
import { FunctionService } from 'src/app/cafe/services/function.service';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { VhBillType2930DetailEdit, VhBillType2930Edit } from '../../bill-type/bill-type-29-30/bill-type2930-interface';

@Component({
  selector: 'app-recieve-goods',
  templateUrl: './recieve-goods.component.html',
  styleUrls: ['./recieve-goods.component.scss']
})
export class RecieveGoodsComponent implements OnInit {
  display_promotion_selling_price: boolean = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').display_promotion_selling_price
  printer: any;
  tableHeight: string;
  public invoiceInfo: any;
  public invoice: VhBillType2930Edit
  invoiceDetail: VhBillType2930DetailEdit[] =  []
  subTotal = 0;
  taxValue = 0
  @Input() id_bill: any;
  branchList: any = this.vhQueryCafe.getlocalBranchs()
  constructor(
    private router: Router,
    private vhQueryCafe: VhQueryCafe,
    private lang: LanguageService,
    private vhAuth: VhAuth,
    private vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private cdRef: ChangeDetectorRef,
    private fncService: FunctionService
  ) {
     
  }
  ngOnInit() {
    let printer = this.vhQueryCafe.getLocalPrintPage('page_desktop_delivery_receive_goods')
    this.printer = printer ? printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)] : { discount_hidden: false, note_hidden: false, fee_hidden: false, tax_hidden: false, debt_hidden: false };
    if (this.printer) {
      this.checkPrinter = this.printer.enable;
    }
  }
  ngAfterViewInit(): void {
    this.getDataInvoice(this.id_bill)

  }
  ngOnDestroy() {
  }
  checkPrinter: boolean = false;

  init(invoice: VhBillType2930Edit = null, invoiceDetail: VhBillType2930DetailEdit[] = null) {
    this.invoice = invoice;
    this.invoiceDetail = invoiceDetail
    if (invoice && invoiceDetail) {
      this.invoiceInfo = {
        employee: this.vhAuth.getlocalEmployee(this.invoice.id_employee),
        branch_name : this.vhQueryCafe.getlocalBranch(invoice.id_branch),
        branch_name_reception: this.vhQueryCafe.getlocalBranch(invoice.id_branch_receive),
      }
    } else this.invoiceInfo = { employee: {} }
    //tính tổng tiền
    let subTotal = 0;
    for (let i of this.invoiceDetail) {
      subTotal += (i.price ? i.price * i.quantity : 0)
    }
    this.subTotal = subTotal;
    this.taxValue = this.invoice.total * (this.invoice.tax / 100)
  }

  getBranchName(id_branch) {
    if (id_branch)
      return this.branchList.find(e => e._id == id_branch).name
    else return ''
  }

  private getDataInvoice(id) {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      Promise.all([
        this.vhQueryCafe.getBill(id),
        this.vhQueryCafe.getBill_details_byId_bill(id)
      ]).then(([invoice, invoiceDetail]: any) => {
        invoice.id_branch_send = invoice.bill_type == 29 ? invoice.id_branch : invoice.id_branch_send;
        invoice.id_branch_receive = invoice.bill_type == 29 ? invoice.id_branch_receive : invoice.id_branch;
        if (!invoiceDetail.length)
          this.init(invoice, [])
        else {
          invoiceDetail = this.vhAlgorithm.sortVietnamesebyASC(invoiceDetail, 'name');
          this.init(invoice, invoiceDetail)
        }
        this.vhComponent.hideLoading(0)

      })
    })

  }
  getUnit(units: any[], ratio) {
    return this.vhQueryCafe.getUnit_byRatio(units, ratio).unit
  }

  getLotNumber(lots, _id) {
    let lot = lots.find(item => item._id == _id)
    return lot.lot_number + '\n' + this.fncService.formatDate(lot.date_mfg) + ' - ' + this.fncService.formatDate(lot.date_exp)
  }
  /**
  * lấy thông tin dữ liệu trang in
  * @returns Promise<any>
  * @example this.checkPrint()
  */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQueryCafe.getLocalPrintPage('page_desktop_delivery_receive_goods')
      if (printer && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }

  printInvoice(goback?) {
    this.checkPrint().then((printer) => {
      if (printer)
        this.vhComponent.showModal(this.renderPrintPage(printer['_id']), { printer: printer, invoice: this.invoice ,invoice_detail: this.invoiceDetail}, false, false, `modal-print-${printer['_id']}`).then(modal => {
          modal.onWillDismiss().then(() => { })
        })
    }, (err) => {
      this.vhComponent.alertMessage(this.lang.translate("Function"), "", this.lang.translate("Please turn on the printer function"), "OK")

    })
  }

  /**
   * trả về component của trang in tương ứng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return ReceiveGoodsK57Col3Component
      case 'print_size_k80_3c': return ReceiveGoodsK80Col3Component
      case 'print_size_k80_4c': return ReceiveGoodsK80Col4Component
      case 'print_size_a5': return ReceiveGoodsA5Component
      case 'print_size_a4': return ReceiveGoodsA4Component
      default: return ReceiveGoodsA4Component
    }
  }
  /**Trở về trang nhận hàng
   * nếu là bill 29 mà có uuid thì update lại
   * nếu là 29 nhưng không có uuid tức là bill này đã chuyển trạng thái, trên db không phải là bill 29 nữa
   */
  goBack() {
    this.vhComponent.hideModal()
  }
  ngAfterViewChecked() {
    if (document.querySelector("#receive-good-detail-invoice") && document.querySelector(".ant-table-thead") && document.querySelector(".receive-good-detail-invoice-header") && document.querySelector(".ant-table-pagination")) {
      this.tableHeight = document.querySelector("#receive-good-detail-invoice").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".receive-good-detail-invoice-header").clientHeight - document.querySelector(".ant-table-pagination").clientHeight - 40 + "px";
    }
    this.cdRef.detectChanges();
  }
}
