import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQueryCafe } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component'; 
import { LanguageService } from 'src/app/cafe/services/language.service';
import { VhBillType9DetailEdit, VhBillType9Edit } from '../../bill-type/bill-type-9/bill-type9-interface';


@Component({
  selector: 'app-openning-stock',
  templateUrl: './openning-stock.component.html',
  styleUrls: ['./openning-stock.component.scss']
})
export class OpenningStockComponent   {
  display_promotion_selling_price: boolean = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').display_promotion_selling_price
  invoice_detail  : VhBillType9DetailEdit[] = []
  subTotal = 0
  tableHeight: string;
  public invoiceInfo: any;
  public invoice: VhBillType9Edit
  @Input() id_bill: any;
  constructor(
    private router: Router,
    private vhQueryCafe: VhQueryCafe,
    private languageService: LanguageService,
    private vhAuth: VhAuth,
    private vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private cdRef: ChangeDetectorRef
  ) { 
    this.init()
  }


  ngAfterViewInit(): void {
    this.getDataInvoice(this.id_bill)
     
  }
  init(invoice: VhBillType9Edit = null, invoiceDetail: VhBillType9DetailEdit[] = null) {
    this.invoice = invoice
    this.invoice_detail = invoiceDetail
    if (invoice && invoiceDetail) {
      this.invoiceInfo = {
        employee: this.vhAuth.getlocalEmployee(this.invoice.id_employee),
      
      }
    } else this.invoiceInfo = { employee: {}, partner: {}, paymentName: '' }
    this.setSubTotal()
  }
  setSubTotal() {
    let subTotal = 0;
    for (let i of this.invoice_detail) {
      subTotal += (i.price ? i.price * i.quantity : 0)
    }
    this.subTotal = subTotal;
  }
  private getDataInvoice(id) {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      Promise.all([
        this.vhQueryCafe.getBill(id),
        this.vhQueryCafe.getBill_details_byId_bill(id)
      ]).then(([invoice, invoiceDetail]: any) => {
        let invoiceDetailFresh: VhBillType9DetailEdit[] = invoiceDetail 
        this.init(invoice, invoiceDetailFresh)

        this.vhComponent.hideLoading(0)
      })
    })

  }

  deleteInvoice() {
    if (this.vhAuth.checkMyPermission("import_enable_cancel_openning_stock")) {
      this.vhComponent.alertConfirm(this.languageService.translate("Confirm"), "", `${this.languageService.translate("Are you sure to cancel the purchase invoice")}?`, "OK", this.languageService.translate("Cancel"))
        .then(() => {
          this.vhComponent.showLoading("", "transparent-loading").then(() => {
            this.vhQueryCafe.deleteBill_Billdetail(this.invoice._id).then(() => {
              this.vhComponent.hideLoading(0).then(() => {
                this.vhComponent.showToast(2000,
                  `${this.languageService.translate("Opening stock")} ${this.invoice.bill_code} ${this.languageService.translate("has been cancelled successfully")}`, "success-toast")
                this.goBack();
              })
            })
          })
        }, () => { })
    }
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  } 

  goBack() {
    this.vhComponent.hideModal()
  }
  ngAfterViewChecked() {
    if (document.querySelector("#opening-stock-detail") && document.querySelector(".ant-table-thead") && document.querySelector(".opening-stock-detail-header")) {
      this.tableHeight = (document.querySelector("#opening-stock-detail").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".opening-stock-detail-header").clientHeight) - 90 + "px";
    }
    this.cdRef.detectChanges();
  }

}
