import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQueryCafe } from 'ionic-vhframeworks';
import { ReturnSupplierA4Component } from 'src/app/cafe/components/print/return-supplier/return-supplier-a4/return-supplier-a4.component';
import { ReturnSupplierA5Component } from 'src/app/cafe/components/print/return-supplier/return-supplier-a5/return-supplier-a5.component';
import { ReturnSupplierK80Col4Component } from 'src/app/cafe/components/print/return-supplier/return-supplier-k80-col4/return-supplier-k80-col4.component';
import { ReturnSupplierK80Col3Component } from 'src/app/cafe/components/print/return-supplier/return-supplier-k80-col3/return-supplier-k80-col3.component';
import { ReturnSupplierK57Col3Component } from 'src/app/cafe/components/print/return-supplier/return-supplier-k57-col3/return-supplier-k57-col3.component';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { VhSupplierReturnInvoiceDetail, VhSupplierReturnInvoices } from 'src/app/cafe/interface/vh-supplier-return-invoice';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { FunctionService } from 'src/app/cafe/services/function.service';
import { VhBillType10DetailEdit, VhBillType10Edit } from '../../bill-type/bill-type-10/bill-type10-interface';

@Component({
  selector: 'app-return-supplier',
  templateUrl: './return-supplier.component.html',
  styleUrls: ['./return-supplier.component.scss']
})
export class ReturnSupplierComponent implements OnInit {
  printer:any;
  tableHeight: string;
  public invoiceInfo: any;
  public invoice: VhBillType10Edit
  invoice_detail  : VhBillType10DetailEdit[] = []
  subTotal = 0
  @Input() id_bill: any;
  display_promotion_selling_price: boolean = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').display_promotion_selling_price
  constructor(
    private router: Router,
    private vhQueryCafe: VhQueryCafe,
    private languageService: LanguageService,
    private vhAuth: VhAuth,
    private vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private cdRef: ChangeDetectorRef,
    private fncService:FunctionService
  ) { 
    
  }

  ngOnInit(): void {  
    let print = this.vhQueryCafe.getLocalPrintPage('page_desktop_return_supplier')
    this.printer =print  ? print.print_sizes[print.print_sizes.findIndex(e => e._id == print.print_size_default)] : {discount_hidden :false, note_hidden: false, fee_hidden: false, tax_hidden: false, debt_hidden: false };
  }
  ngAfterViewInit(): void {
    this.getDataInvoice(this.id_bill)
     
  }
  init(invoice: VhBillType10Edit = null, invoiceDetail: VhBillType10DetailEdit[] = null) {
    this.invoice = invoice
    this.invoice_detail = invoiceDetail
    if (invoice && invoiceDetail) {
      this.invoiceInfo = {
        employee: this.vhAuth.getlocalEmployee(this.invoice.id_employee),
        partner: this.vhQueryCafe.getlocalSupplier(this.invoice.id_supplier),
        paymentName: this.setPaymentName(this.invoice.payment_type)
      }
    } else this.invoiceInfo = { employee: {}, partner: {}, paymentName: '' }
    this.setSubTotal()
  }
  setSubTotal() {
    let subTotal = 0;
    for (let i of this.invoice_detail) {
      subTotal += (i.price ? i.price * i.quantity : 0)
    }
    this.subTotal = subTotal;
  }
  private getDataInvoice(id) {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      Promise.all([
        this.vhQueryCafe.getBill(id),
        this.vhQueryCafe.getBill_details_byId_bill(id)
      ]).then(([invoice, invoiceDetail]: any) => {
        let invoiceDetailFresh: VhBillType10DetailEdit[] = invoiceDetail 
        invoiceDetailFresh = this.vhAlgorithm.sortVietnamesebyASC(invoiceDetailFresh, 'name');
        console.log(invoice);
        
        this.init(invoice, invoiceDetailFresh)

        this.vhComponent.hideLoading(0)
      })
    })

  }

  getUnit(units: any[], ratio) {
    return this.vhQueryCafe.getUnit_byRatio(units,ratio).unit
  }

  getLotNumber(lots, _id){
    let lot = lots.find(item => item._id == _id)
    return lot.lot_number + '\n' + this.fncService.formatDate(lot.date_mfg) + ' - ' +  this.fncService.formatDate(lot.date_exp)
  } 
  deleteInvoice() {
    if (this.vhAuth.checkMyPermission("return_enable_cancel_supplier")) {
      this.vhComponent.alertConfirm(this.languageService.translate("Confirm"), "", `${this.languageService.translate("Are you sure to cancel the purchase invoice")}?`, "OK", this.languageService.translate("Cancel"))
        .then(() => {
          this.vhComponent.showLoading("", "transparent-loading").then(() => {
            this.vhQueryCafe.deleteBill_Billdetail(this.invoice._id).then(() => {
              this.vhComponent.hideLoading(0).then(() => {
                this.vhComponent.showToast(2000,
                  `${this.languageService.translate("Return supplier")} ${this.invoice.bill_code} ${this.languageService.translate("has been cancelled successfully")}`, "success-toast")
                this.goBack();
              })
            })
          })
        }, () => { })
    }
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  public setPaymentName(type: 1 | 2 | 3) {
    switch (type) {
      case 1: return this.languageService.translate("Cash")
      case 2: return this.languageService.translate("Debit")
      case 3: return this.vhQueryCafe.getlocalWallet(this.invoice.id_wallet).name
    }
  }
  public renderAddress(partner) {
    return (partner.address ? partner.address : '') +
      (partner.district ? ', ' + partner.district : '') +
      (partner.province ? ', ' + partner.province : '') +
      (partner.country ? ', ' + partner.country : '')
  } 
  /**
  * lấy thông tin dữ liệu trang in
  * @returns Promise<any>
  * @example this.checkPrint()
  */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQueryCafe.getLocalPrintPage('page_desktop_return_supplier')
      if (printer  && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }

  printInvoice() {
    this.checkPrint().then((printer) => {
      if (printer)
        this.vhComponent.showModal(this.renderPrintPage(printer['_id']), { printer: printer ,invoice_detail: this.invoice_detail, invoice: this.invoice }, false, false, `modal-print-${printer['_id']}`)
    }, (err) => {
      let state : any ="Return customer"
      this.vhComponent.alertMessage(this.languageService.translate("Function"), "", this.languageService.translate("Please turn on the printer function"), "OK")
       
    })
  }

  /**
   * trả về component của trang in tương ứng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return ReturnSupplierK57Col3Component
      case 'print_size_k80_3c': return ReturnSupplierK80Col3Component
      case 'print_size_k80_4c': return ReturnSupplierK80Col4Component
      case 'print_size_a5': return ReturnSupplierA5Component
      case 'print_size_a4': return ReturnSupplierA4Component
      default: return ReturnSupplierA4Component
    }
  }
  goBack() {
    this.vhComponent.hideModal();
  }
  ngAfterViewChecked() {
    if (document.querySelector("#return-invoice-supplier-detail") && document.querySelector(".ant-table-thead") && document.querySelector(".return-invoice-supplier-detail-header") && document.querySelector(".ant-table-pagination")) {
      this.tableHeight = document.querySelector("#return-invoice-supplier-detail").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".return-invoice-supplier-detail-header").clientHeight - document.querySelector(".ant-table-pagination").clientHeight - 40 + "px";
    }
    this.cdRef.detectChanges();
  }

}
